<script>
import { TimelineMax, Elastic } from 'gsap'
import $ from 'jquery'

export default {
  data () {
    return {
      inHand: true
    }
  },
  methods: {
    handAnimation () {
      /* -------------------- */
      /*         Hand         */
      /* -------------------- */
      const handTl = new TimelineMax({ repeat: -1, repeatDelay: 0.3 })
      handTl.add('start', '+=0')
        .set('#hand #main', { rotation: -10, transformOrigin: '50% 90%' }, 'start')
        .to('#hand #main', 1, { ease: Elastic.easeOut, rotation: 10, transformOrigin: '50% 90%' }, 'start')
        .to('#hand #main', 1, { ease: Elastic.easeOut, rotation: -10, transformOrigin: '50% 90%' }, 'start+=1.4')

        .set('#finger2', { ease: Elastic.easeOut, rotation: -30, transformOrigin: '100% 100%' }, 'start')
        .to('#finger2', 1.2, { ease: Elastic.easeOut, rotation: 35, transformOrigin: '90% 100%' }, 'start')
        .to('#finger2', 1.2, { ease: Elastic.easeOut, rotation: -30, transformOrigin: '100% 100%' }, 'start+=1.4')

        .set('#finger3', { ease: Elastic.easeOut, rotation: -33, transformOrigin: '70% 108%' }, 'start')
        .to('#finger3', 1.2, { ease: Elastic.easeOut, rotation: 35, transformOrigin: '70% 105%' }, 'start+=0.02')
        .to('#finger3', 1.2, { ease: Elastic.easeOut, rotation: -33, transformOrigin: '70% 108%' }, 'start+=1.42')

        .set('#finger4', { ease: Elastic.easeOut, rotation: -30, transformOrigin: '10% 100%' }, 'start')
        .to('#finger4', 1.2, { ease: Elastic.easeOut, rotation: 30, transformOrigin: '0% 95%' }, 'start+=0.04')
        .to('#finger4', 1.2, { ease: Elastic.easeOut, rotation: -30, transformOrigin: '15% 100%' }, 'start+=1.44')

        .set('#finger5', { ease: Elastic.easeOut, rotation: -30, transformOrigin: '10% 78%' }, 'start')
        .to('#finger5', 1.2, { ease: Elastic.easeOut, rotation: 30, transformOrigin: '10% 70%' }, 'start+=0.06')
        .to('#finger5', 1.2, { ease: Elastic.easeOut, rotation: -30, transformOrigin: '10% 78%' }, 'start+=1.46')

      /* -------------------- */
      /*     Decoraction      */
      /* -------------------- */
      let duration = 0

      const circleTl = new TimelineMax({ repeat: -1, yoyo: true })
      circleTl
        .staggerTo('#hand .circle', 0.5, { attr: {r: 5} }, Math.random() * 0.5 + 0.1)

      const blinkTl = new TimelineMax({ repeat: -1, onRepeat: function () { this.duration(Math.random() * 0.5 + 0.5) } })
      blinkTl
        .staggerTo('#hand .dot', Math.random() * 0.2 + 0.5, { repeat: 1, yoyo: true, repeatDelay: duration / 2 })
        .staggerTo('#hand .dot', Math.random() * 0.2 + 0.5, { repeat: 1, yoyo: true, autoAlpha: 1 }, Math.random() * 0.5 + 0.5)

      const crossTl = new TimelineMax({ repeat: -1, onRepeat: function () { this.duration(Math.random() * 0.5 + 0.8) } })
      crossTl
        .staggerTo('#hand .cross', Math.random() * 0.2 + 0.8, { repeat: 1, yoyo: true, repeatDelay: duration / 2, transformOrigin: 'center center', scaleX: 1.3, scaleY: 1.3 })
        .staggerTo('#hand .cross', Math.random() * 0.2 + 0.8, { repeat: 1, yoyo: true, scaleX: 1.3, scaleY: 1.3 }, Math.random() * 0.5 + 0.8)

      const flowerTl = new TimelineMax({repeat: -1, repeatDelay: 1.5})
      flowerTl.timeScale(0.5)
        .to('#hand .line', 0.05, { attr: {x1: 245, x2: 248} })
        .to('#hand .line', 0.35, { attr: {x1: 250, x2: 250} })
        .to('#hand .line', 0.2, { autoAlpha: 0 }, '-=0.1')

      if (!this.inHand) {
        handTl.pause()

        $('#handBox').hover(
          function () {
            handTl.play()
          }
        )
        $('#handBox').mouseleave(
          function () {
            handTl.pause()
          }
        )
      }
    }
  },
  mounted () {
    this.handAnimation()
  },
  created () {
    this.inHand = this.$route.name === 'hand'
  }
}
</script>

