        <line class="line s-2 stroke_red" x1="245" y1="155" x2="245" y2="155"/>
        <line class="line s-2 stroke_red" x1="245" y1="155" x2="245" y2="155" transform="rotate(45,243,155)"/>
        <line class="line s-2 stroke_red" x1="245" y1="155" x2="245" y2="155" transform="rotate(90,243,155)"/>
        <line class="line s-2 stroke_red" x1="245" y1="155" x2="245" y2="155" transform="rotate(135,243,155)"/>
        <line class="line s-2 stroke_red" x1="245" y1="155" x2="245" y2="155" transform="rotate(180,243,155)"/>
        <line class="line s-2 stroke_red" x1="245" y1="155" x2="245" y2="155" transform="rotate(225,243,155)"/>
        <line class="line s-2 stroke_red" x1="245" y1="155" x2="245" y2="155" transform="rotate(270,243,155)"/>
        <line class="line s-2 stroke_red" x1="245" y1="155" x2="245" y2="155" transform="rotate(315,243,155)"/>
      </g>
    </g>
  </svg>
  </div>
</div>
</template>

<script>
import { TimelineMax, Power0, Elastic } from 'gsap'
import $ from 'jquery'

export default {
  data () {
    return {
      inSpider: true
    }
  },
  methods: {
    spiderAnimation () {
      const spiderTl = new TimelineMax({ repeat: -1, repeatDelay: 0.8 })
      spiderTl.add('start', '+=0')
        .set('#spider #main', { x: 2, y: 340, scale: 0.8, transformOrigin: 'center' }, 'start')
        .set('#spider #decoraction', { x: -60, y: 320 }, 'start')
        .to('#spider #main', 2.5, { ease: Elastic.easeOut, y: 510 }, 'start')
        .to('#spider #decoraction', 2.5, { ease: Elastic.easeOut, y: 390 }, 'start')
        .to('#downline', 2.5, { ease: Elastic.easeOut, y: 170 }, 'start')
        .to('#flexline', 2.5, { ease: Elastic.easeOut, attr: {y1: 162} }, 'start')

        .to('#spider #main', 1.5, { ease: Power0.easeNone, y: 340 }, 'start+2.5')
        .to('#spider #decoraction', 1.5, { ease: Power0.easeNone, y: 320 }, 'start+2.5')
        .to('#downline', 1.5, { ease: Power0.easeNone, y: 0 }, 'start+2.5')
        .to('#flexline', 1.4, { ease: Power0.easeNone, attr: {y1: 3} }, 'start+2.5')

      /* -------------------- */
      /*     Decoraction      */
      /* -------------------- */
      let duration = 0

      const circleTl = new TimelineMax({ repeat: -1, yoyo: true })
      circleTl
        .staggerTo('#spider .circle', 0.5, { attr: {r: 5} }, Math.random() * 0.5 + 0.1)

      const blinkTl = new TimelineMax({ repeat: -1, onRepeat: function () { this.duration(Math.random() * 0.5 + 0.5) } })
      blinkTl
        .staggerTo('#spider .dot', Math.random() * 0.2 + 0.5, { repeat: 1, yoyo: true, repeatDelay: duration / 2 })
        .staggerTo('#spider .dot', Math.random() * 0.2 + 0.5, { repeat: 1, yoyo: true, autoAlpha: 1 }, Math.random() * 0.5 + 0.5)

      const crossTl = new TimelineMax({ repeat: -1, onRepeat: function () { this.duration(Math.random() * 0.5 + 0.8) } })
      crossTl
        .staggerTo('#spider .cross', Math.random() * 0.2 + 0.8, { repeat: 1, yoyo: true, repeatDelay: duration / 2, transformOrigin: 'center center', scaleX: 1.3, scaleY: 1.3 })
        .staggerTo('#spider .cross', Math.random() * 0.2 + 0.8, { repeat: 1, yoyo: true, scaleX: 1.3, scaleY: 1.3 }, Math.random() * 0.5 + 0.8)

      const flowerTl = new TimelineMax({repeat: -1, repeatDelay: 1.5})
      flowerTl
        .to('#spider .line', 0.05, { attr: {x1: 245, x2: 248} })
        .to('#spider .line', 0.35, { attr: {x1: 250, x2: 250} })
        .to('#spider .line', 0.2, { autoAlpha: 0 }, '-=0.1')

      if (!this.inSpider) {
        spiderTl.pause()

        $('#spiderBox').hover(
          function () {
            spiderTl.play()
          }
        )
        $('#spiderBox').mouseleave(
          function () {
            spiderTl.pause()
          }
        )
      }
    }
  },
  mounted () {
    this.spiderAnimation()
  },
  created () {
    this.inSpider = this.$route.name === 'spider'
  }
}
</script>

<style scoped>
</style>



