<template lang="pug">
div#animation(class="bg-light")
  Navbar(:nav-item-color="Title.bgColor" v-if="Title.bgColor")
  Sidebar(:new-title="Title" v-if="Title.titleText")
  .container(class="py-5")
    .form-row(class="mt-gutter-s")
      .col-4
        router-link(to="/spider")
          #spiderBox(class="box bg-spider rounded h-100")
            spider(class="spider")

      .col-8
        router-link(to="/candle")
          #candleBox(class="box bg-candle rounded")
            candle(class="candle")
        router-link(to="/pumpkin")
          #pumpkinBox(class="longbox bg-pumpkin mt-gutter-s rounded")
            pumpkin(class="pumpkin")

    .form-row(class="mt-gutter-s")
      .col-12
        router-link(to="/gost")
          #gostBox(class="box bg-gost rounded")
            gost(class="gost")

    .form-row(class="mt-gutter-s")
      .col-6
        router-link(to="/pot")
          #potBox(class="longbox bg-pot rounded")
            pot(class="pot")
      .col-6
        router-link(to="/hand")
          #handBox(class="longbox bg-hand rounded")
            hand(class="hand")
</template>

<script>
import Navbar from './Navbar'
import Sidebar from './Sidebar/Sidebar'
import gost from './Pages/Animation/gost'
import pot from './Pages/Animation/pot'
import hand from './Pages/Animation/hand'
import candle from './Pages/Animation/candle'
import pumpkin from './Pages/Animation/pumpkin'
import spider from './Pages/Animation/spider'

export default {
  name: 'Dashboard',
  components: {
    Navbar,
    Sidebar,
    gost,
    pot,
    hand,
    candle,
    pumpkin,
    spider
  },
  data () {
    return {
      Title: {
        titleText: 'MBE 小動畫',
        subText: 'MBE STYLE<br>FLAT ANIMATION',
        bgColor: '#95afc0'
      }
    }
  },
  created () {
  }
}
</script>

<style scoped>
</style>



