        <path d="M87,93.5c2.2,0,4-1.8,4-4h-8C83,91.7,84.8,93.5,87,93.5z"/>
        <path d="M83,73.5c0,2.2-1.8,4-4,4c-2.2,0-4-1.8-4-4h-8c0,2.2-1.8,4-4,4c-2.2,0-4-1.8-4-4h-8v16h8c0-2.2,1.8-4,4-4
          c2.2,0,4,1.8,4,4h8c0-2.2,1.8-4,4-4c2.2,0,4,1.8,4,4h8v-16H83z"/>
      </g>

      <g id="shadow_main">
        <circle class="color_purple" cx="165.5" cy="39" r="9"/>

        <circle class="color_purple" cx="156.5" cy="75" r="9"/>
        <rect x="156.5" y="66" class="color_purple" width="10" height="18"/>

        <circle class="color_purple" cx="165.5" cy="111" r="9"/>

        <circle class="color_purple tail1" cx="246.5" cy="21" r="9"/>
        <rect x="210.5" y="12" class="color_purple tail1" width="36" height="18"/>

        <circle class="color_purple tail2" cx="246.5" cy="57" r="9"/>
        <rect x="210.5" y="48" class="color_purple tail2" width="36" height="18"/>

        <circle class="color_purple tail3" cx="237.5" cy="93" r="9"/>
        <rect x="210.5" y="84" class="color_purple tail3" width=27 height="18"/>

        <circle class="color_purple tail4" cx="237.5" cy="129" r="9"/>
        <rect x="210.5" y="120" class="color_purple tail4" width="27" height="18"/>
      </g>

      <g id="body_droplets">
        <circle class="color_body op-0 droplet1" cx="192.5" cy="21" r="9"/>
        <circle class="color_body op-0 droplet3" cx="201.5" cy="93" r="9"/>
      </g>

      <circle class="color_bg" cx="228.5" cy="39" r="9"/>
      <rect x="228.5" y="30" class="color_bg" width="18" height="18"/>
      <circle class="color_bg" cx="237.5" cy="75" r="9"/>
      <circle class="color_bg" cx="219.5" cy="111" r="9"/>
      <rect x="219.5" y="102" class="color_bg" width="27" height="18"/>

      <g id="shadow_droplets">
        <circle class="color_purple op-0 droplet2" cx="282.5" cy="57" r="9"/>
        <circle class="color_purple op-0 droplet4" cx="282.5" cy="129" r="9"/>
      </g>

      <g id="color_white">
        <path class="color_white" d="M21.5,75c0-33.3,25.8-60.5,58.5-62.8c-1.5-0.1-3-0.2-4.5-0.2c-34.8,0-63,28.2-63,63s28.2,63,63,63
          c1.5,0,3-0.1,4.5-0.2C47.3,135.5,21.5,108.3,21.5,75z"/>
        <path class="s-6 stroke_white" d="M66.5,23.1c5.6-2,11.7-3.1,18-3.1"/>
