      <line class="s-6 stroke_black ground3" x1="276" y1="183" x2="285" y2="183"/>
      <line class="s-6 stroke_black ground4" x1="297" y1="183" x2="327" y2="183"/>
      <line class="s-6 stroke_black ground5" x1="327" y1="183" x2="327" y2="183"/>
    </g>

  <!-- -------------------- -->
  <!--      Decoraction     -->
  <!-- -------------------- -->
    <g style="transform: translateX(240px) translateY(-90px) scale(0.8);">
      <circle class="circle stroke_blue s-2" cx="40" cy="40" r="0"/>
      <circle class="circle stroke_yellow s-2" cx="120" cy="55" r="0"/>

      <circle class="dot op-0 color_white" cx="105" cy="65" r="1"/>
      <circle class="dot op-0 color_white" cx="20" cy="75" r="1"/>
      <circle class="dot op-0 color_white" cx="80" cy="100" r="2"/>
      <circle class="dot op-0 color_white" cx="100" cy="30" r="2"/>

      <g class="cross">
        <line class="stroke_yellow s-2" x1="62" y1="50" x2="68" y2="50"/>
        <line class="stroke_yellow s-2" x1="65" y1="47" x2="65" y2="53"/>
      </g>

      <g class="cross">
        <line class="stroke_blue s-3" x1="6" y1="90" x2="16" y2="90"/>
        <line class="stroke_blue s-3" x1="11" y1="85" x2="11" y2="95"/>
      </g>

      <g class="cross">
        <line class="stroke_blue s-2" x1="135" y1="60" x2="141" y2="60"/>
        <line class="stroke_blue s-2" x1="138" y1="57" x2="138" y2="63"/>
      </g>

      <g class="flower">
        <line class="line s-3 stroke_yellow" x1="50" y1="80" x2="50" y2="80"/>
        <line class="line s-3 stroke_yellow" x1="50" y1="80" x2="50" y2="80" transform="rotate(45,45,80)"/>
        <line class="line s-3 stroke_yellow" x1="50" y1="80" x2="50" y2="80" transform="rotate(90,45,80)"/>
        <line class="line s-3 stroke_yellow" x1="50" y1="80" x2="50" y2="80" transform="rotate(135,45,80)"/>
        <line class="line s-3 stroke_yellow" x1="50" y1="80" x2="50" y2="80" transform="rotate(180,45,80)"/>
        <line class="line s-3 stroke_yellow" x1="50" y1="80" x2="50" y2="80" transform="rotate(225,45,80)"/>
        <line class="line s-3 stroke_yellow" x1="50" y1="80" x2="50" y2="80" transform="rotate(270,45,80)"/>
        <line class="line s-3 stroke_yellow" x1="50" y1="80" x2="50" y2="80" transform="rotate(315,45,80)"/>
      </g>
    </g>
    </svg>
  </div>
</div>
</template>

<script>
import { TimelineMax, Power0 } from 'gsap'
import $ from 'jquery'

export default {
  data () {
    return {
      inGost: true
    }
  },
  methods: {
    gostAnimation () {
      /* -------------------- */
      /*         Gost         */
      /* -------------------- */
      const gostTl = new TimelineMax({ repeat: -1, yoyo: true })
      gostTl
        .to('#gost #main', 0.5, { transformOrign: 'center', scaleX: 1.04, scaleY: 1.02 })

      const bodyTl = new TimelineMax({ repeat: -1 })
      bodyTl.add('start', '+=0')
        .to('#gost .color_body.tail1', 0.4, { attr: {width: 36, cx: 192.5} }, 'start')
        .to('#gost .color_body.tail1', 0.3, { attr: {width: 9, cx: 165.5} }, 'start+0.4')
        .to('#gost .color_body.tail1', 0.3, { attr: {width: 36, cx: 192.5} }, 'start+0.7')
        .to('#gost .color_body.tail1', 0.4, { attr: {width: 9, cx: 165.5} }, 'start+1.0')
        .to('#gost .color_body.droplet1', 0, { autoAlpha: 1 }, 'start+=0.3')
        .to('#gost .color_body.droplet1', 0.4, { attr: {cx: 201.5} }, 'start+=0.3')
        .to('#gost .color_body.droplet1', 0.4, { attr: {r: 0} }, 'start+=0.7')
        .to('#gost .color_body.tail2', 0.4, { attr: {width: 9, cx: 174.5} }, 'start')
        .to('#gost .color_body.tail2', 0.3, { attr: {width: 18, cx: 183.5} }, 'start+0.4')
        .to('#gost .color_body.tail2', 0.3, { attr: {width: 0, cx: 165.5} }, 'start+0.7')
        .to('#gost .color_body.tail2', 0.4, { attr: {width: 27, cx: 192.5} }, 'start+1.0')
        .to('#gost .color_body.tail3', 0.4, { attr: {width: 36, cx: 192.5} }, 'start')
        .to('#gost .color_body.tail3', 0.3, { attr: {width: 18, cx: 174.5} }, 'start+0.4')
        .to('#gost .color_body.tail3', 0.3, { attr: {width: 45, cx: 201.5} }, 'start+0.7')
        .to('#gost .color_body.tail3', 0.4, { attr: {width: 18, cx: 174.5} }, 'start+1.0')
        .to('#gost .color_body.droplet3', 0, { autoAlpha: 1 }, 'start+=0.9')
        .to('#gost .color_body.droplet3', 0.4, { attr: {cx: 205} }, 'start+=0.9')
        .to('#gost .color_body.droplet3', 0.2, { attr: {r: 0} }, 'start+=1.3')
        .to('#gost .color_body.tail4', 0.4, { attr: {width: 26, cx: 191.5} }, 'start')
        .to('#gost .color_body.tail4', 0.3, { attr: {width: 0, cx: 165.5} }, 'start+0.4')
        .to('#gost .color_body.tail4', 0.3, { attr: {width: 23, cx: 188.5} }, 'start+0.7')
        .to('#gost .color_body.tail4', 0.4, { attr: {width: 14, cx: 179.5} }, 'start+1.0')

      const shadowTl = new TimelineMax({ repeat: -1 })
      shadowTl.add('start', '+=0')
        .to('#gost .color_purple.tail1', 0.3, { attr: {width: 63, cx: 272.5} }, 'start')
        .to('#gost .color_purple.tail1', 0.4, { attr: {width: 27, cx: 237.5} }, 'start+=0.3')
        .to('#gost .color_purple.tail1', 0.3, { attr: {width: 63, cx: 272.5} }, 'start+=0.7')
        .to('#gost .color_purple.tail1', 0.5, { attr: {width: 36, cx: 246.5} }, 'start+=1.2')
        .to('#gost .color_purple.tail2', 0.6, { attr: {width: 74, cx: 282.5} }, 'start')
        .to('#gost .color_purple.tail2', 0.5, { attr: {width: 36, cx: 246.5} }, 'start+=0.6')
        .to('#gost .color_purple.tail2', 0.3, { attr: {width: 65, cx: 273.5} }, 'start+=1.1')
        .to('#gost .color_purple.tail2', 0.2, { attr: {width: 36, cx: 246.5} }, 'start+=1.5')
        .to('#gost .color_purple.droplet2', 0, { autoAlpha: 1 }, 'start+=0.6')
        .to('#gost .color_purple.droplet2', 0.4, { attr: {cx: 291.5} }, 'start+=0.6')
        .to('#gost .color_purple.droplet2', 0.4, { attr: {r: 0} }, 'start+=0.8')
        .to('#gost .color_purple.tail3', 0.3, { attr: {width: 63, cx: 272.5} }, 'start+=0.1')
        .to('#gost .color_purple.tail3', 0.5, { attr: {width: 27, cx: 237.5} }, 'start+=0.4')
        .to('#gost .color_purple.tail3', 0.3, { attr: {width: 63, cx: 272.5} }, 'start+=0.9')
        .to('#gost .color_purple.tail3', 0.5, { attr: {width: 27, cx: 237.5} }, 'start+=1.2')
        .to('#gost .color_purple.tail4', 0.3, { attr: {width: 72, cx: 282.5} }, 'start+=0.2')
        .to('#gost .color_purple.tail4', 0.4, { attr: {width: 27, cx: 237.5} }, 'start+=0.5')
        .to('#gost .color_purple.tail4', 0.4, { attr: {width: 72, cx: 282.5} }, 'start+=0.9')
        .to('#gost .color_purple.tail4', 0.4, { attr: {width: 27, cx: 237.5} }, 'start+=1.3')
        .to('#gost .color_purple.droplet4', 0, { autoAlpha: 1 }, 'start+=1.3')
        .to('#gost .color_purple.droplet4', 0.2, { attr: {cx: 291.5} }, 'start+=1.3')
        .to('#gost .color_purple.droplet4', 0.2, { attr: {r: 0} }, 'start+=1.5')
        .to('#gost .border1', 0.4, { attr: {x1: 183.5, x2: 219.5} }, 'start')
        .to('#gost .border1', 0.3, { attr: {x1: 183.5, x2: 192.5} }, 'start+=0.4')
        .to('#gost .border1', 0.3, { attr: {x1: 183.5, x2: 227.5} }, 'start+=0.7')
        .to('#gost .border1', 0.5, { attr: {x1: 183.5, x2: 201.5} }, 'start+=1.2')
        .to('#gost .border2', 0.4, { attr: {x1: 138.5, x2: 219.5} }, 'start')
        .to('#gost .border2', 0.5, { attr: {x1: 138.5, x2: 192.5} }, 'start+=0.4')
        .to('#gost .border2', 0.4, { attr: {x1: 138.5, x2: 245.5} }, 'start+=0.9')
        .to('#gost .border2', 0.3, { attr: {x1: 138.5, x2: 201.5} }, 'start+=1.3')

      /* -------------------- */
      /*       Ground         */
      /* -------------------- */
      const groundTl = new TimelineMax({ repeat: -1 })
      groundTl.add('start', '+=0').timeScale(3)
        .set('#gost #ground', { scaleX: -1, transformOrigin: 'center' })
        // [1]
        .to('#gost .ground1', 0.15, { ease: Power0.easeNone, attr: {x1: 3, x2: 3} }, 'start')
        .to('#gost .ground1', 0.05, { ease: Power0.easeNone, strokeWidth: '0', attr: {x1: 0, x2: 0} }, 'start+=0.15')
        .to('#gost .ground1', 0.00, { ease: Power0.easeNone, strokeWidth: '6', attr: {x1: 327, x2: 327} }, 'start+=4')
        .to('#gost .ground1', 0.15, { ease: Power0.easeNone, attr: {x1: 318, x2: 327} }, 'start+=4')
        .to('#gost .ground1', 0.75, { ease: Power0.easeNone, attr: {x1: 276, x2: 285} }, 'start+=4.15')
        // [2]
        .to('#gost .ground2', 0.15, { ease: Power0.easeNone, attr: {x1: 15, x2: 255} }, 'start')
        .to('#gost .ground2', 0.05, { ease: Power0.easeNone, attr: {x1: 12, x2: 252} }, 'start+=0.15')
        .to('#gost .ground2', 0.15, { ease: Power0.easeNone, attr: {x1: 3, x2: 243} }, 'start+=0.2')
        .to('#gost .ground2', 4.35, { ease: Power0.easeNone, attr: {x1: 3, x2: 3} }, 'start+=0.35')
        .to('#gost .ground2', 0.05, { ease: Power0.easeNone, strokeWidth: '0', attr: {x1: 0, x2: 0} }, 'start+=4.7')
        // [3]
        .to('#gost .ground3', 0.15, { ease: Power0.easeNone, attr: {x1: 267, x2: 276} }, 'start')
        .to('#gost .ground3', 0.05, { ease: Power0.easeNone, attr: {x1: 264, x2: 273} }, 'start+=0.15')
        .to('#gost .ground3', 0.15, { ease: Power0.easeNone, attr: {x1: 255, x2: 264} }, 'start+=0.2')
        .to('#gost .ground3', 4.35, { ease: Power0.easeNone, attr: {x1: 15, x2: 24} }, 'start+=0.35')
        .to('#gost .ground3', 0.05, { ease: Power0.easeNone, attr: {x1: 12, x2: 21} }, 'start+=4.7')
        .to('#gost .ground3', 0.15, { ease: Power0.easeNone, attr: {x1: 3, x2: 12} }, 'start+=4.75')
        // [4]
        .to('#gost .ground4', 0.15, { ease: Power0.easeNone, attr: {x1: 288, x2: 327} }, 'start')
        .to('#gost .ground4', 0.05, { ease: Power0.easeNone, attr: {x1: 285, x2: 327} }, 'start+=0.15')
        .to('#gost .ground4', 0.15, { ease: Power0.easeNone, attr: {x1: 276, x2: 327} }, 'start+=0.2')
        .to('#gost .ground4', 3.45, { ease: Power0.easeNone, attr: {x1: 87, x2: 327} }, 'start+=0.35')
        .to('#gost .ground4', 0.90, { ease: Power0.easeNone, attr: {x1: 36, x2: 276} }, 'start+=3.8')
        .to('#gost .ground4', 0.05, { ease: Power0.easeNone, attr: {x1: 33, x2: 273} }, 'start+=4.7')
        .to('#gost .ground4', 0.15, { ease: Power0.easeNone, attr: {x1: 24, x2: 264} }, 'start+=4.75')
        // [5]
        .to('#gost .ground5', 0.0, { ease: Power0.easeNone, strokeWidth: '0' }, 'start')
        .to('#gost .ground5', 0.0, { ease: Power0.easeNone, strokeWidth: '6', attr: {x1: 327, x2: 327} }, 'start+=4.4')
        .to('#gost .ground5', 0.5, { ease: Power0.easeNone, strokeWidth: '6', attr: {x1: 297, x2: 327} }, 'start+=4.4')

      /* -------------------- */
      /*     Decoraction      */
      /* -------------------- */
      let duration = 0

      const circleTl = new TimelineMax({ repeat: -1, yoyo: true })
      circleTl
        .staggerTo('#gost .circle', 0.5, { attr: {r: 5} }, Math.random() * 0.5 + 0.1)

      const blinkTl = new TimelineMax({ repeat: -1, onRepeat: function () { this.duration(Math.random() * 0.5 + 0.5) } })
      blinkTl
        .staggerTo('#gost .dot', Math.random() * 0.2 + 0.5, { repeat: 1, yoyo: true, repeatDelay: duration / 2 })
        .staggerTo('#gost .dot', Math.random() * 0.2 + 0.5, { repeat: 1, yoyo: true, autoAlpha: 1 }, Math.random() * 0.5 + 0.5)

      const crossTl = new TimelineMax({ repeat: -1, onRepeat: function () { this.duration(Math.random() * 0.5 + 0.8) } })
      crossTl
        .staggerTo('#gost .cross', Math.random() * 0.2 + 0.8, { repeat: 1, yoyo: true, repeatDelay: duration / 2, transformOrigin: 'center center', scaleX: 1.3, scaleY: 1.3 })
        .staggerTo('#gost .cross', Math.random() * 0.2 + 0.8, { repeat: 1, yoyo: true, scaleX: 1.3, scaleY: 1.3 }, Math.random() * 0.5 + 0.8)

      const flowerTl = new TimelineMax({repeat: -1, repeatDelay: 1.5})
      flowerTl
        .to('#gost .line', 0.05, { attr: {x1: 50, x2: 53} })
        .to('#gost .line', 0.35, { attr: {x1: 57, x2: 57} })
        .to('#gost .line', 0.2, { autoAlpha: 0 }, '-=0.1')

      if (!this.inGost) {
        gostTl.pause()
        bodyTl.pause()
        shadowTl.pause()
        groundTl.pause()

        $('#gostBox').hover(
          function () {
            gostTl.play()
            bodyTl.play()
            shadowTl.play()
            groundTl.play()
          }
        )
        $('#gostBox').mouseleave(
          function () {
            gostTl.pause()
            bodyTl.pause()
            shadowTl.pause()
            groundTl.pause()
          }
        )
      }
    }
  },
  mounted () {
    this.gostAnimation()
  },
  created () {
    this.inGost = this.$route.name === 'gost'
  }
}
</script>

<style scoped>
</style>



