<script>
import { TimelineMax, Circ, Power1, Power3, Power4 } from 'gsap'
import $ from 'jquery'

export default {
  data () {
    return {
      inPumpkin: true
    }
  },
  methods: {
    pumpkinAnimation () {
      /* -------------------- */
      /*       Pumpkin        */
      /* -------------------- */
      function pumpkinTl1 () {
        return new TimelineMax({ repeat: 8, yoyo: true })
          .add('start', '+=0').timeScale(0.9)
          .set('#pumpkin #body', { y: 4 })
          .set('#pumpkin #ground', { y: 4 })
          .to('#pumpkin #body', 0.3, { scaleX: 1, scaleY: 1.02, transformOrigin: '50% bottom' }, 'start')
          .to('#pumpkin #body', 0.2, { scaleX: 1.03, scaleY: 0.94, transformOrigin: '50% bottom' }, 'start+0.3')
      }

      function pumpkinTl2 () {
        return new TimelineMax({ repeat: 0 })
          .add('start', '+=4.3').timeScale(0.9)
          .to('#pumpkin #body', 0.1, { scaleX: 1.03, scaleY: 0.94, transformOrigin: '50% bottom' }, 'start')
          // change color to yellow
          .to('#pumpkin #eyes circle', 0.1, { fill: '#FCFC00' }, 'start')
          .to('#pumpkin #mouth path', 0.1, { fill: '#FCFC00' }, 'start')
          .to('#pumpkin #ground rect', 0.1, { fill: '#FCFC00' }, 'start')
          .to('.pumpkinBG', 0.1, { backgroundColor: '#FCFC00' }, 'start')
          // shake
          .to('#pumpkin #eyes rect', 0.1, { attr: {y: 88} }, 'start')
          .to('#pumpkin #face', 0.05, { x: -2.4 }, 'start')
          .to('#pumpkin #face', 0.05, { repeat: 18, yoyo: true, x: 2.4 }, 'start+0.05')
          .to('#pumpkin #face', 0.05, { x: 0 }, 'start+0.8')
          .to('#pumpkin #body', 0.05, { repeat: 18, yoyo: true, x: -2 }, 'start')
          .to('#pumpkin #body', 0.05, { x: 0 }, 'start+0.8')
          // change color back
          .to('#pumpkin #eyes rect', 0.4, { attr: {y: 57} }, 'start+0.85')
          .to('#pumpkin #eyes circle', 0.2, {fill: '#000000'}, 'start+0.855')
          .to('#pumpkin #mouth path', 0.2, {fill: '#000000'}, 'start+0.855')
          .to('#pumpkin #ground rect', 0.2, {fill: '#48BEFE'}, 'start+0.855')
          .to('.pumpkinBG', 0.2, {backgroundColor: '#48BEFE'}, 'start+0.855')
          .to('#pumpkin #body', 0.3, { scaleX: 1, scaleY: 1, transformOrigin: '50% bottom' }, 'start+0.855')
      }

      /* -------------------- */
      /*         Bats         */
      /* -------------------- */
      function batTl () {
        return new TimelineMax({ repeat: 0 })
          .add('start', '+=4.85')
          .set('.bat', { x: 112.5, y: 165, transformOrigin: '50% bottom', autoAlpha: 1 })
          .set('.bat1', { rotation: 10, scaleX: 0.8, scaleY: 0.8 }, 'start')
          .to('.bat1', 0.9, { x: 150, y: -80, ease: Circ.easeOut }, 'start')
          .set('.bat2', { rotation: -20, scaleX: 0.7, scaleY: 0.7 }, 'start')
          .to('.bat2', 0.9, { x: 10, y: -80, ease: Power4.easeOut }, 'start')
          .set('.bat3', { rotation: -10, scaleX: 0.6, scaleY: 0.6 }, 'start')
          .to('.bat3', 1, { x: 70, y: -80 }, 'start')
          .set('.bat4', { rotation: 30, scaleX: 0.6, scaleY: 0.6 }, 'start')
          .to('.bat4', 0.7, { x: 210, y: -80 }, 'start')
          .set('.bat5', { rotation: 40, scaleX: 0.5, scaleY: 0.5 }, 'start')
          .to('.bat5', 1.2, { x: 240, y: -80 }, 'start')
          .set('.bat6', { rotation: -30, scaleX: 0.6, scaleY: 0.6, ease: Power3.easeOut }, 'start')
          .to('.bat6', 1.3, { x: 0, y: -80 }, 'start')
          .to('.bat', 0.9, { autoAlpha: 0, ease: Power1.easeIn }, 'start')
      }

      const allPumpkinTl = new TimelineMax({ repeat: -1 })
      allPumpkinTl
        .add('start', '+=0')
        .add(pumpkinTl1(), 'start')
        .add(pumpkinTl2(), 'start')
        .add(batTl(), 'start')

      /* -------------------- */
      /*     Decoraction      */
      /* -------------------- */
      let duration = 0

      const circleTl = new TimelineMax({ repeat: -1, yoyo: true })
      circleTl
        .staggerTo('#pumpkin .circle', 0.5, { attr: {r: 5} }, Math.random() * 0.5 + 0.1)

      const blinkTl = new TimelineMax({ repeat: -1, onRepeat: function () { this.duration(Math.random() * 0.5 + 0.5) } })
      blinkTl
        .staggerTo('#pumpkin .dot', Math.random() * 0.2 + 0.5, { repeat: 1, yoyo: true, repeatDelay: duration / 2 })
        .staggerTo('#pumpkin .dot', Math.random() * 0.2 + 0.5, { repeat: 1, yoyo: true, autoAlpha: 1 }, Math.random() * 0.5 + 0.5)

      const crossTl = new TimelineMax({ repeat: -1, onRepeat: function () { this.duration(Math.random() * 0.5 + 0.8) } })
      crossTl
        .staggerTo('#pumpkin .cross', Math.random() * 0.2 + 0.8, { repeat: 1, yoyo: true, repeatDelay: duration / 2, transformOrigin: 'center center', scaleX: 1.3, scaleY: 1.3 })
        .staggerTo('#pumpkin .cross', Math.random() * 0.2 + 0.8, { repeat: 1, yoyo: true, scaleX: 1.3, scaleY: 1.3 }, Math.random() * 0.5 + 0.8)

      const flowerTl = new TimelineMax({repeat: -1, repeatDelay: 1.5})
      flowerTl
        .to('#pumpkin .line', 0.05, { attr: {x1: 275, x2: 278} })
        .to('#pumpkin .line', 0.35, { attr: {x1: 280, x2: 280} })
        .to('#pumpkin .line', 0.2, { autoAlpha: 0 }, '-=0.1')

      if (!this.inPumpkin) {
        allPumpkinTl.pause()
        $('#pumpkinBox').hover(
          function () {
            allPumpkinTl.play()
          }
        )
        $('#pumpkinBox').mouseleave(
          function () {
            allPumpkinTl.pause()
          }
        )
      }
    }
  },
  mounted () {
    this.pumpkinAnimation()
  },
  created () {
    this.inPumpkin = this.$route.name === 'pumpkin'
  }
}
</script>

<style scoped>
</style>



