        <path class="s-6 stroke_black" d="M84,214.6c-2.6,7.7-4.3,15.9-5.1,24.5l0,4.6c0,6.5,5.2,11.7,11.7,11.7s11.7-5.3,11.7-11.7v-0.4V259v0.4
