<script>
import { TimelineMax, Power3 } from 'gsap'
import $ from 'jquery'

export default {
  data () {
    return {
      inCandle: true
    }
  },
  methods: {
    candleAnimation () {
      /* -------------------- */
      /*        Candle        */
      /* -------------------- */
      const candleTl = new TimelineMax({ repeat: -1, repeatDelay: 1 })
      candleTl.add('start', '+=0')
        .to('.c3', 1.8, { attr: {r: 137.5} }, 'start')
        .to('.c2', 1.3, { attr: {r: 92.5} }, 'start')
        .to('#light', 1.3, { scaleX: 0.8, scaleY: 0.8, transformOrigin: 'center' }, 'start')
        .to('#mainfire', 1.3, { scaleX: 0.9, scaleY: 0.9, transformOrigin: '50% bottom' }, 'start')
        .to('.fire', 1.3, { autoAlpha: 0.95 }, 'start')
        .to('.eye', 1.3, { y: 2 }, 'start')
        .to('.eyelid', 1.3, { y: 6 }, 'start')

        .to('.c3', 0.8, { ease: Power3.easeOut, attr: {r: 157.5} }, 'start+=1.8')
        .to('.c2', 0.3, { ease: Power3.easeOut, attr: {r: 102.5} }, 'start+=1.8')
        .to('#light', 0.3, { ease: Power3.easeOut, scaleX: 1, scaleY: 1, transformOrigin: 'center' }, 'start+=1.8')
        .to('#mainfire', 0.3, { ease: Power3.easeOut, scaleX: 1, scaleY: 1, transformOrigin: '50% bottom' }, 'start+=1.8')
        .to('.fire', 0.3, { autoAlpha: 1 }, 'start+=1.8')
        .to('.eye', 0.3, { y: 0 }, 'start+=1.8')
        .to('.eyelid', 0.3, { y: 1 }, 'start+=1.8')

        .to('.c3', 1.8, { attr: {r: 137.5} }, 'start+=2.8')
        .to('.c2', 1.3, { attr: {r: 92.5} }, 'start+=2.8')
        .to('#light', 1.3, { scaleX: 0.8, scaleY: 0.8, transformOrigin: 'center' }, 'start+=2.8')
        .to('#mainfire', 1.3, { scaleX: 0.9, scaleY: 0.9, transformOrigin: '50% bottom' }, 'start+=2.8')
        .to('.fire', 1.3, { autoAlpha: 0.95 }, 'start+=2.8')
        .to('.eye', 1.3, { y: 2 }, 'start+=2.8')
        .to('.eyelid', 1.3, { y: 6 }, 'start+=2.8')

        .to('.c3', 0.8, { ease: Power3.easeOut, attr: {r: 157.5} }, 'start+=4.6')
        .to('.c2', 0.3, { ease: Power3.easeOut, attr: {r: 102.5} }, 'start+=4.6')
        .to('#light', 0.3, { ease: Power3.easeOut, scaleX: 1, scaleY: 1, transformOrigin: 'center' }, 'start+=4.6')
        .to('#mainfire', 0.3, { ease: Power3.easeOut, scaleX: 1, scaleY: 1, transformOrigin: '50% bottom' }, 'start+=4.6')
        .to('.fire', 0.3, { autoAlpha: 1 }, 'start+=4.6')
        .to('.eye', 0.3, { y: 0 }, 'start+=4.6')
        .to('.eyelid', 0.3, { y: 2 }, 'start+=4.6')

        .to('.c3', 3, { attr: {r: 92.5} }, 'start+=5.6')
        .to('.c2', 2, { attr: {r: 92.5} }, 'start+=5.6')
        .to('#light', 2, { scaleX: 0.5, scaleY: 0.5, transformOrigin: 'center' }, 'start+=5.6')
        .to('#mainfire', 2, { scaleX: 0.7, scaleY: 0.7, transformOrigin: '50% bottom' }, 'start+=5.6')
        .to('.fire', 2, { autoAlpha: 0.9 }, 'start+=5.6')
        .to('.eye', 2, { y: 2 }, 'start+=5.6')
        .to('.eyelid', 2, { y: 8 }, 'start+=5.6')

        .to('.c3', 0.6, { ease: Power3.easeOut, attr: {r: 157.5} }, 'start+=8.6')
        .to('.c2', 0.3, { ease: Power3.easeOut, attr: {r: 102.5} }, 'start+=8.6')
        .to('#light', 0.3, { ease: Power3.easeOut, scaleX: 1, scaleY: 1, transformOrigin: 'center' }, 'start+=8.6')
        .to('#mainfire', 0.3, { ease: Power3.easeOut, scaleX: 1, scaleY: 1, transformOrigin: '50% bottom' }, 'start+=8.6')
        .to('.fire', 0.3, { autoAlpha: 1 }, 'start+=8.6')
        .to('.eye', 0.3, { y: 0 }, 'start+=8.6')
        .to('.eyelid', 0.3, { y: -2 }, 'start+=8.6')

        .to('.eye', 0.05, { ease: Power3.easeOut, attr: {r: 7}, scaleX: 0.5, scaleY: 1.1, transformOrigin: 'center' }, 'start+=8.6')
        .to('.eye', 0.1, { attr: {r: 6}, scaleX: 1, scaleY: 1 }, 'start+=8.9')
        .to('.c3.color_white', 0.05, { autoAlpha: 0.1, scaleX: 1.2, scaleY: 1.2, transformOrigin: 'center' }, 'start+=8.6')
        .to('.c3.color_white', 0.5, { autoAlpha: 0, scaleX: 1, scaleY: 1 }, 'start+=8.9')

      /* -------------------- */
      /*      Decoraction     */
      /* -------------------- */
      let duration = 0

      const circleTl = new TimelineMax({ repeat: -1, yoyo: true })
      circleTl
        .staggerTo('#candle .circle', 0.5, { attr: {r: 5} }, Math.random() * 0.5 + 0.1)

      const blinkTl = new TimelineMax({ repeat: -1, onRepeat: function () { this.duration(Math.random() * 0.5 + 0.5) } })
      blinkTl
        .staggerTo('#candle .dot', Math.random() * 0.2 + 0.5, { repeat: 1, yoyo: true, repeatDelay: duration / 2 })
        .staggerTo('#candle .dot', Math.random() * 0.2 + 0.5, { repeat: 1, yoyo: true, autoAlpha: 1 }, Math.random() * 0.5 + 0.5)

      const crossTl = new TimelineMax({ repeat: -1, onRepeat: function () { this.duration(Math.random() * 0.5 + 0.8) } })
      crossTl
        .staggerTo('#candle .cross', Math.random() * 0.2 + 0.8, { repeat: 1, yoyo: true, repeatDelay: duration / 2, transformOrigin: 'center center', scaleX: 1.3, scaleY: 1.3 })
        .staggerTo('#candle .cross', Math.random() * 0.2 + 0.8, { repeat: 1, yoyo: true, scaleX: 1.3, scaleY: 1.3 }, Math.random() * 0.5 + 0.8)

      const flowerTl = new TimelineMax({repeat: -1, repeatDelay: 1.5})
      flowerTl.timeScale(0.5)
        .to('#camdle .line', 0.05, { attr: {x1: 235, x2: 238} })
        .to('#camdle .line', 0.35, { attr: {x1: 240, x2: 240} })
        .to('#camdle .line', 0.2, { autoAlpha: 0 }, '-=0.1')

      if (!this.inCandle) {
        candleTl.pause()

        $('#candleBox').hover(
          function () {
            candleTl.play()
          }
        )
        $('#candleBox').mouseleave(
          function () {
            candleTl.pause()
          }
        )
      }
    }
  },
  mounted () {
    this.candleAnimation()
  },
  created () {
    this.inCandle = this.$route.name === 'candle'
  }
}
</script>

<style scoped>
</style>



