        <path class="color_shadow" d="M158.5,3.1c-2.9,0.1-5.7,0.2-8.5,0.3c46.4,2,81.5,11.1,81.5,22.1c0,11-35.1,20.2-81.5,22.1
          c2.8,0.1,5.6,0.2,8.5,0.3c50.5-1.1,90-10.7,90-22.4C248.5,13.8,209,4.2,158.5,3.1z"/>
        <path class="s-6 stroke_black" d="M234.9,13.8c9.3,3.4,14.6,7.4,14.6,11.7c0,12.4-44.8,22.5-100,22.5s-100-10.1-100-22.5c0-3.3,3.2-6.5,9-9.3"/>
        <path class="s-6 stroke_black" d="M68.5,12.3c18.2-5.6,47.7-9.3,81-9.3c29.8,0,56.6,2.9,74.9,7.6"/>
      </g>

      <g class="bubble1">
        <circle class="s-6 stroke_green" cx="155.2" cy="17.1" r="14.3"/>
        <circle class="color_green" cx="157.2" cy="20.3" r="11.6"/>
      </g>

      <path class="color_green" d="M149.5,18c32.3,0,58.5,4,58.5,9s-26.2,9-58.5,9S91,32,91,27S117.2,18,149.5,18"/>
      <path class="s-6 stroke_black" d="M178.4,19.2c17.4,1.6,29.1,4.5,29.1,7.8c0,5-26.2,9-58.5,9h0.5C117.2,36,91,32,91,27c0-3.3,11.7-6.2,29.1-7.8"/>

      <g class="bubble2">
        <circle class="s-6 stroke_green" cx="181.2" cy="20.7" r="7.5"/>
        <circle class="color_green" cx="180.9" cy="23.5" r="6.1"/>
      </g>

      <g id="face">
        <circle cx="122.5" cy="94" r="6"/>
        <circle cx="176.5" cy="94" r="6"/>
        <circle class="color_green" cx="113.5" cy="103" r="5"/>
        <circle class="color_green" cx="185.5" cy="103" r="5"/>
        <g>
          <path d="M133.5,100c-2.2,0-4,1.8-4,4h8C137.5,101.8,135.7,100,133.5,100z"/>
          <path d="M133.5,124c2.2,0,4-1.8,4-4h-8C129.5,122.2,131.3,124,133.5,124z"/>
          <path d="M149.5,124c2.2,0,4-1.8,4-4h-8C145.5,122.2,147.3,124,149.5,124z"/>
          <path d="M149.5,100c-2.2,0-4,1.8-4,4h8C153.5,101.8,151.7,100,149.5,100z"/>
          <path d="M165.5,100c-2.2,0-4,1.8-4,4h8C169.5,101.8,167.7,100,165.5,100z"/>
          <path d="M165.5,124c2.2,0,4-1.8,4-4h-8C161.5,122.2,163.3,124,165.5,124z"/>
          <path d="M161.5,104c0,2.2-1.8,4-4,4s-4-1.8-4-4h-8c0,2.2-1.8,4-4,4s-4-1.8-4-4h-8v16h8c0-2.2,1.8-4,4-4s4,1.8,4,4h8
            c0-2.2,1.8-4,4-4s4,1.8,4,4h8v-16H161.5z"/>
        </g>
        <g id="water1">
          <circle class="color_green" cx="165.5" cy="144" r="4"/>
          <rect x="153.5" y="128" transform="matrix(-1.836970e-16 1 -1 -1.836970e-16 297.5 -33.5)" class="color_green" width="24" height="8"/>
        </g>
        <g id="water2">
          <circle class="color_green" cx="149.5" cy="131" r="4"/>
          <rect x="144" y="121.5" transform="matrix(-1.836970e-16 1 -1 -1.836970e-16 275 -24)" class="color_green" width="11" height="8"/>
          <line class="s-8 stroke_green op-0" x1="149.5" y1="124.6" x2="149.5" y2="133.6"/>
        </g>
      </g>
    </g>

    <g id="ground">
      <rect x="3" y="219" class="color_bg" width="293" height="20"/>
      <line class="s-6 stroke_black" x1="22.8" y1="216" x2="247.8" y2="216"/>
      <line class="s-6 stroke_black" x1="3.2" y1="216" x2="9.2" y2="216"/>
      <line class="s-6 stroke_black" x1="277.8" y1="216.5" x2="295.8" y2="216.5"/>
      <line class="s-6 stroke_black" x1="259.8" y1="216.5" x2="265.8" y2="216.5"/>
    </g>

    <!-- -------------------- -->
    <!--      Decoraction     -->
    <!-- -------------------- -->
    <g id="decoraction" style="transform: translateX(345px) translateY(-135px) scale(1.3) scaleX(-1)">
      <circle class="circle stroke_blue s-2" cx="230" cy="100" r="0"/>
      <circle class="circle stroke_white s-2" cx="35" cy="125" r="0"/>

      <circle class="dot op-0 color_white" cx="240" cy="90" r="1"/>
      <circle class="dot op-0 color_white" cx="45" cy="118" r="1"/>
      <circle class="dot op-0 color_white" cx="255" cy="115" r="1"/>
      <circle class="dot op-0 color_white" cx="62" cy="108" r="1"/>
      <circle class="dot op-0 color_white" cx="72" cy="138" r="2"/>

      <g class="cross">
        <line class="stroke_white s-2" x1="262" y1="118" x2="268" y2="118"/>
        <line class="stroke_white s-2" x1="265" y1="115" x2="265" y2="121"/>
      </g>

      <g class="cross">
        <line class="stroke_blue s-2" x1="48" y1="100" x2="54" y2="100"/>
        <line class="stroke_blue s-2" x1="51" y1="97" x2="51" y2="103"/>
      </g>

      <g class="flower">
        <line class="line s-2 stroke_blue" x1="235" y1="135" x2="235" y2="135"/>
        <line class="line s-2 stroke_blue" x1="235" y1="135" x2="235" y2="135" transform="rotate(45,233,135)"/>
        <line class="line s-2 stroke_blue" x1="235" y1="135" x2="235" y2="135" transform="rotate(90,233,135)"/>
        <line class="line s-2 stroke_blue" x1="235" y1="135" x2="235" y2="135" transform="rotate(135,233,135)"/>
        <line class="line s-2 stroke_blue" x1="235" y1="135" x2="235" y2="135" transform="rotate(180,233,135)"/>
        <line class="line s-2 stroke_blue" x1="235" y1="135" x2="235" y2="135" transform="rotate(225,233,135)"/>
        <line class="line s-2 stroke_blue" x1="235" y1="135" x2="235" y2="135" transform="rotate(270,233,135)"/>
        <line class="line s-2 stroke_blue" x1="235" y1="135" x2="235" y2="135" transform="rotate(315,233,135)"/>
      </g>
    </g>
    </svg>
  </div>
</div>
</template>

<script>
import { TimelineMax, Power0, Power1, Power3, Power4 } from 'gsap'
import $ from 'jquery'

export default {
  data () {
    return {
      inPot: true
    }
  },
  methods: {
    potAnimation () {
      /* -------------------- */
      /*          Pot         */
      /* -------------------- */
      const potTl = new TimelineMax({ repeat: -1, yoyo: true, repeatDelay: 0.1 })
      potTl.add('start', '+=0')
        .to('#pot #main', 1, { ease: Power4.easeOut, scaleX: 1, scaleY: 1.01, transformOrigin: '50% bottom' }, 'start')
        .to('#pot #main', 0.8, { ease: Power4.easeIn, scaleX: 1.01, scaleY: 0.99, transformOrigin: '50% bottom' }, 'start+0.5')

      const bubbleTl = new TimelineMax({ repeat: -1, yoyo: true })
      bubbleTl.add('start', '+=0')
        .to('.bubble1', 1.8, { ease: Power0.easeNone, y: 4, rotation: -80, transformOrigin: 'center' }, 'start')
        .to('.bubble2', 1.2, { ease: Power0.easeNone, y: -1, rotation: -40, transformOrigin: 'center' }, 'start')
        .to('.up .bubble1', 1.8, { ease: Power0.easeNone, x: 10 }, 'start')
        .to('.up .bubble2', 1.2, { ease: Power0.easeNone, x: -3 }, 'start')

      const waterTl1 = new TimelineMax({ repeat: -1 })
      waterTl1.add('start', '+=0')
        .to('#water1 circle', 0.9, { ease: Power1.easeOut, y: 8 }, 'start')
        .to('#water1 circle', 0.7, { ease: Power1.easeIn, y: 4 }, 'start+=0.9')
        .to('#water1 circle', 0.9, { ease: Power1.easeOut, y: 10 }, 'start+=1.6')
        .to('#water1 circle', 1.1, { ease: Power1.easeIn, y: 0 }, 'start+=2.5')
        .to('#water1 rect', 0.9, { ease: Power1.easeOut, attr: {width: 32} }, 'start')
        .to('#water1 rect', 0.7, { ease: Power1.easeIn, attr: {width: 28} }, 'start+=0.9')
        .to('#water1 rect', 0.9, { ease: Power1.easeOut, attr: {width: 34} }, 'start+=1.6')
        .to('#water1 rect', 1.1, { ease: Power1.easeIn, attr: {width: 24} }, 'start+=2.5')

      const waterTl2 = new TimelineMax({ repeat: -1, repeatDelay: 0.7 })
      waterTl2.add('start', '+=0')
        .to('#water2 circle', 0.7, { ease: Power1.easeOut, y: 6 }, 'start+=0.3')
        .to('#water2 circle', 0.9, { ease: Power1.easeIn, y: 2 }, 'start+=1.0')
        .to('#water2 circle', 0.5, { ease: Power1.easeOut, y: 4 }, 'start+=1.9')
        .to('#water2 circle', 0.6, { ease: Power1.easeIn, y: 0 }, 'start+=2.4')
        .to('#water2 rect', 0.7, { ease: Power1.easeOut, attr: {width: 17} }, 'start+=0.3')
        .to('#water2 rect', 0.9, { ease: Power1.easeIn, attr: {width: 13} }, 'start+=1.0')
        .to('#water2 rect', 0.5, { ease: Power1.easeOut, attr: {width: 15} }, 'start+=1.9')
        .to('#water2 rect', 0.6, { ease: Power1.easeIn, attr: {width: 11} }, 'start+=2.4')

        .to('#water2 line', 0, { autoAlpha: 1 }, 'start+=1.7')
        .to('#water2 line', 1.0, { ease: Power3.easeIn, y: 93 }, 'start+=1.7')
        .to('#water2 line', 0.2, { scaleY: 1.2 }, 'start+=2.2')
        .to('#water2 line', 0.1, { scaleY: 1 }, 'start+=2.5')
        .to('#water2 line', 0.1, { attr: {y1: 125, y2: 125} }, 'start+=2.6')
      /* -------------------- */
      /*      Decoraction     */
      /* -------------------- */
      let duration = 0

      const circleTl = new TimelineMax({ repeat: -1, yoyo: true })
      circleTl
        .staggerTo('#pot .circle', 0.5, { attr: {r: 5} }, Math.random() * 0.5 + 0.1)

      const blinkTl = new TimelineMax({ repeat: -1, onRepeat: function () { this.duration(Math.random() * 0.5 + 0.5) } })
      blinkTl
        .staggerTo('#pot .dot', Math.random() * 0.2 + 0.5, { repeat: 1, yoyo: true, repeatDelay: duration / 2 })
        .staggerTo('#pot .dot', Math.random() * 0.2 + 0.5, { repeat: 1, yoyo: true, autoAlpha: 1 }, Math.random() * 0.5 + 0.5)

      const crossTl = new TimelineMax({ repeat: -1, onRepeat: function () { this.duration(Math.random() * 0.5 + 0.8) } })
      crossTl
        .staggerTo('#pot .cross', Math.random() * 0.2 + 0.8, { repeat: 1, yoyo: true, repeatDelay: duration / 2, transformOrigin: 'center center', scaleX: 1.3, scaleY: 1.3 })
        .staggerTo('#pot .cross', Math.random() * 0.2 + 0.8, { repeat: 1, yoyo: true, scaleX: 1.3, scaleY: 1.3 }, Math.random() * 0.5 + 0.8)

      const flowerTl = new TimelineMax({repeat: -1, repeatDelay: 1.5})
      flowerTl.timeScale(0.5)
        .to('#pot #decoraction .line', 0.05, { attr: {x1: 235, x2: 238} })
        .to('#pot #decoraction .line', 0.35, { attr: {x1: 240, x2: 240} })
        .to('#pot #decoraction .line', 0.2, { autoAlpha: 0 }, '-=0.1')

      if (!this.inPot) {
        potTl.pause()
        bubbleTl.pause()
        waterTl1.pause()
        waterTl2.pause()

        $('#potBox').hover(
          function () {
            potTl.play()
            bubbleTl.play()
            waterTl1.play()
            waterTl2.play()
          }
        )
        $('#potBox').mouseleave(
          function () {
            potTl.pause()
            bubbleTl.pause()
            waterTl1.pause()
            waterTl2.pause()
          }
        )
      }
    }
  },
  mounted () {
    this.potAnimation()
  },
  created () {
    this.inPot = this.$route.name === 'pot'
  }
}
</script>

<style scoped>
</style>



